    display: flex;
    justify-content: center;
    align-items: center;
}

.clicker__container {
    position: absolute;
    bottom: 100%;
    right: 0;

    display: flex;
    flex-direction: column-reverse;
    align-items: flex-end;

    background-color: #f7f7f7e6;
    border-radius: 10px;
    max-width: 50vw;
}

.clicker__actionButtons {
    padding: 10px;
    max-width: 100%;
}
.clicker__actionButton {
    display: flex;
    align-items: center;

    border: 1px solid var(--red);
    padding: 0.5rem 0.75rem;
    border-radius: 2rem;
    white-space: nowrap;
    transition: all 0.3s ease;
}
.clicker__actionButton:hover,
.clicker__actionButton:focus,
.clicker__actionButton:active {
    background-color: #e8e8e8;
}
.clicker__actionButton:not(:last-of-type) {
    margin-bottom: 0.5rem;
}

@media (max-width: 768px) {
    .clicker__actionButton {
        border: 0;
        border-radius: 0;
        padding: 0.25rem;
    }

    .clicker__actionButton:not(:last-of-type) {
        margin-bottom: 0.25rem;
    }
}

.linkIcon {
    display: inline-block;
    width: 28px;
    height: 20px;

    background: url("data:image/svg+xml;base64,") no-repeat left;
    background-size: contain;
}

.clicker__contextMenu {
    border-radius: 10px;
    padding: 5px;
